.mb-boxes .nav > li > a:hover, .mb-boxes .nav > li > a:focus {
  background-color: #C0D0D3;
}
.mb-boxes .nav > li.active > a {
  background-color: #5d9cec !important;
  color: #fff;
}
@media (min-width: 992px) {
  .mb-boxes {
    height: auto !important;
    visibility: visible !important;
    display: block;
  }
}

@media (min-width: 992px) {
  .mb-compose-button {
    display: block;
    width: 100%;
  }

  .mb-toggle-button {
    display: none;
  }
}
.mb-mails > tbody > tr > td {
  border-top-color: transparent;
  cursor: pointer;
}
.mb-mails .mb-mail-active {
  background-color: #f7f8f9 !important;
}
.mb-mails .mb-mail-avatar {
  float: left;
  margin-right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 3px;
}
.mb-mails .mb-mail-date {
  display: block;
  float: right;
  color: #909FA7;
  font-size: 12px;
  font-weight: bold;
}
.mb-mails .mb-mail-from {
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
}
.mb-mails .mb-mail-meta {
  overflow: hidden;
}
.mb-mails .mb-mail-subject {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.1;
  margin-bottom: 6px;
}
.mb-mails .mb-mail-preview {
  clear: both;
  margin-top: 10px;
  color: #909FA7;
  margin-top: 2px;
}
.mb-mails .mb-attachment {
  margin-top: 30px;
  padding-top: 30px;
  border-top: 1px solid #e4eaec;
}

.mb-view > tbody > tr > td {
  cursor: auto;
}
.mb-view .mb-mail-to-list > a {
  margin-right: 5px;
  color: #909FA7;
}

/* ========================================================================
   Component: Plan table
 ========================================================================== */
.plan {
  background-color: #edf1f2;
  overflow: auto;
  border-radius: 0.25rem;
  padding-bottom: 30px;
}
.plan .plan-header {
  background-color: #23b7e5;
  color: #fff;
  margin: 0;
  padding: 20px 10px;
  text-align: center;
}
.plan .plan-price {
  background: #fff;
  padding: 16px 8px;
  text-align: center;
}
.plan .plan-price h2 {
  margin: 0;
}
.plan .plan-period {
  font-size: 16px;
}
.plan hr {
  display: block;
  margin: 24px;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  height: 0;
}
.plan .plan-description {
  padding: 0 24px;
  text-align: center;
}
.plan .plan-features {
  list-style-type: none;
  padding-left: 0;
  margin-top: 25px;
  margin-bottom: 25px;
  text-align: left;
}
.plan .plan-features > li {
  padding: 8px 24px;
}
.plan .plan-features > li > em {
  color: #23b7e5;
  margin-right: 7px;
}
.plan .plan-features > li:last-child {
  padding-bottom: 0;
}
.plan .plan-feature-disable {
  color: #f05050 !important;
}

.ng-invalid.ng-touched {
  border-color: #f05050;
}